import React, { useEffect, useState } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, compareJSON, parseDateMMDDYYYY } from "../../../stores/Utils";
import { generateTranslateMarriageCertification } from "../services";
import { TextInput_Cmpt } from "../../../components";
import { PickClient_Cmpt, NotaryAgentPick_Cmpt } from "../../Customer";
import { OpenModal } from "../../../components/DialogHRM";
import { DateCalComponent_Cmpt } from "../../Inventory";

const TextInput = TextInput_Cmpt();

const frmNm = "marriage_certificate"


const PickClient = PickClient_Cmpt();


const DateCalComponent =DateCalComponent_Cmpt();

const NotaryAgentPick = NotaryAgentPick_Cmpt();




let flds = [
	"folio",
	"tomo",
	"state_registration",
	"registration_officer",
	"revised_by",
	"prepared_by",
	"city_office",
	"state_office",
	"city_registration",
	"dop",
	"dom",
	"marriage_registration_officer",
	"spouse2Id"
]






const MarriageCertification  = ({ data, elmId, updObs, close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	


	const loadDoc  = async ()  => {
		let bdyq2 = {
			query: "getScanDocumentsNotary",
			params: {
				":customerId" : getStore("upd_client").clientNotaryId,
				":documentType": "MCT"
			},
			queryString: "!* contain :customerId AND documentType contain :documentType",
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		

		
		if (td && !td.error) {
				let docId = ObjectKeys(td.data)[0]
				let user = getStore("upd_client")
				if(td.data[docId]){
					updStore(frmNm, td.data[docId]);
					updStore(frmNm + "_bck", JSON.parse(JSON.stringify(td.data[docId])));
					
					let frm_userInfo = parseData2Spouse(user);

					if(td.data[docId]?.spouse1Id === user.clientNotaryId){
						updStore("MCT_spouse1", frm_userInfo);
						let spouse = parseData2Spouse(td.data[docId]?.spouse2Info);
						updStore("MCT_spouse1", frm_userInfo);
						updStore("MCT_spouse2", spouse);
					}else if(td.data[docId]?.spouse2Id === user.clientNotaryId){
						let spouse = parseData2Spouse(td.data[docId]?.spouse1Info);
						updStore("MCT_spouse2", frm_userInfo);
						updStore("MCT_spouse1", spouse);
					}
					set_obs(gen16CodeId())
				}else{
					
					updStore("MCT_spouse1", null);
				}
				
			
		}

	}



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			setTimeout(() => {
				loadDoc();
			},50);
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v) => {
		let frm2 = getStore(frmNm) || {};
		frm2[f] = v;
		updStore(frmNm, frm2);
		 
		set_obs(gen16CodeId())
	};


	const print  = async ()  => {




		let arflsd = [
			"name",
			"city_pob",
			"state_pob",
			"dob",
			"dateOfBirth",
			"fatherName",
			"motherName",
		]
		let newObj = {}
		arflsd.map(fld=>{
			newObj[fld+"_sp2"] = getStore("MCT_spouse2")[fld];
		})
		let ffm = Object.assign({}, getStore(frmNm),getStore("MCT_spouse1"), newObj);


		if(ffm["dom"]){
			let nd = (new Date(ffm["dom"])).getTime();
			if(nd){
				ffm["date_of_married"] = parseDate(nd);
			}
		}

		if(ffm["dop"]){
			let nd = (new Date(ffm["dop"])).getTime();
			if(nd){
				ffm["date_of_peticion"] = parseDate(nd);
			}
		}
		
		if(ffm["dateOfBirth"]){
			ffm["dob_p"] = parseDate(ffm["dateOfBirth"]);
		}

		if(ffm["dateOfBirth_sp2"]){
			ffm["dob_p_sp2"] = parseDate(ffm["dateOfBirth_sp2"]);
		}


		

		ffm["city2_registration"] =ffm["city_registration"] 
		ffm["state2_registration"] =ffm["state_registration"] 
		

		ffm["todayDate"] = parseDateMMDDYYYY((new Date()).getTime());


		let pdfUri = await generateTranslateMarriageCertification(ffm, true, true);
		window.open(pdfUri);
	};



	
	const save  = async ()  => {

		let ffm =  getStore(frmNm);

		let bdyq2 = {};
		if(ffm["documentNotaryId"]){
			let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )
		
			bdyq2 = {
				query: "updateDocumentsNotary",
				params:{
					"documentNotaryId": ffm["documentNotaryId"]
				},
				data2update: diff.data
			};
		}
		else{
			ffm["spouse1Id"] = getStore("upd_client").clientNotaryId;
			ffm["documentType"] = "MCT";
			ffm["documentNotaryId"] = gen16CodeId();
			bdyq2 = {
				query: "addDocumentsNotary",
				form:ffm
			};
		}
	
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			//console.log(JSON.stringify(td));
			updStore(frmNm, td.data);
			set_obs(gen16CodeId())
			close(true)
		}
	}


	const handlePick = (v,f)=>{
		
		updFrm(f,v.clientNotaryId);
		let spouse2 = parseData2Spouse(v);
		updStore("MCT_spouse2",spouse2);

	}

	const openPick = (f)=>{
		let data = {};
		data["zIndex"] = 450;
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = <PickClient handleClick={(v)=>handlePick(v,f) } />
		OpenModal(data);
	}


	const handleNotary  =  (f,v)  => {
		updFrm(f,v)
		//set_obs(gen16CodeId())
	}

	const openAddNotary =  (f) => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <NotaryAgentPick updObs={set_obs} confirm={(v)=>handleNotary(f,v)} />
		OpenModal(dataModal);
	}


	return (
		<div style={{
			maxHeight: "80vh",
			width: "800px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
				Traducion de Certificacion de Matrimonio
				
				</p>
				<div className="flexSpace"/>
				<button className="group_title purple addBtn" onClick={print}>
					Imprimir
				</button>
			</div>

			

			<p className="group_title">
				Lugar y datos del Matrimonio 
			</p>
			<div className="_dsplFlx spaceAround"> 
				<TextInput  init={getStore(frmNm)?.["folio"]} label={"Folio"} width={60} updChanges={(e)=>updFrm("folio",e)}/>
				<TextInput  init={getStore(frmNm)?.["tomo"]} label={"Tomo"} width={60} updChanges={(e)=>updFrm("tomo",e)}/>
				<DateCalComponent  
					init={getStore(frmNm)?.["dom"]} 
					label={"Fecha de matrimonio"} 
					updDate={(e)=>updFrm("dom",e)}
				/>
				<div className="_dsplFlx "> 
					<div>
						<p className="label" onClick={()=>openAddNotary("marriage_registration_officer")}>
							{"Nombre de la Notaria:"}
						</p>
						<div className="date2Upd">
							{getStore(frmNm)?.["marriage_registration_officer"]}
						</div>
					</div>
				</div>
			</div>

			

			<div className="_dsplFlx spaceAround"> 
				<TextInput  init={getStore(frmNm)?.["city_registration"]} label={"Municipio"} width={240} updChanges={(e)=>updFrm("city_registration",e)}/>
				<TextInput  
					init={getStore(frmNm)?.["state_registration"]} 
					label={"Provincia"} width={240} 
					updChanges={(e)=>updFrm("state_registration",e)}
				/>
			</div>
			

			
			
			
			<div className="separator"/>
			

			
			<p className="group_title">
				Oficina donde se solitito el Documento 
			</p>
			<div className="_dsplFlx spaceAround"> 
				<DateCalComponent  
					init={getStore(frmNm)?.["dop"]} 
					label={"Fecha de solicitud"} 
					updDate={(e)=>updFrm("dop",e)}
				/>
				<TextInput  init={getStore(frmNm)?.["city_office"]} label={"Municipio"} width={240} updChanges={(e)=>updFrm("city_office",e)}/>
				<TextInput  init={getStore(frmNm)?.["state_office"]} label={"Provincia"} width={240} updChanges={(e)=>updFrm("state_office",e)}/>
			</div>
			<div className="_dsplFlx spaceAround flxWrp" style={{margin: "22px 0 12px"}}> 
				
				<div>
					<p className="label" onClick={()=>openAddNotary("prepared_by")}>
						{"Preparado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["prepared_by"]}
					</div>
				</div>
				<div>
					<p className="label" onClick={()=>openAddNotary("revised_by")}>
						{"Revisado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["revised_by"]}
					</div>
				</div>

			
				<div>
					<p className="label" onClick={()=>openAddNotary("registration_officer")}>
						{"Firmado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["registration_officer"]}
					</div>
				</div>
				
			</div>






			<div className="separator"/>


			<div className="_dsplFlx " style={{margin: "9px 0"}}>
				<div>
					<p className="group_title">
						Conyuge (1)
					</p>
					<TextView  
						init={getStore("MCT_spouse1")?.["name"]} 
						label={"Nombre"} 
						width={240} 
					/>
				</div>
				<div className="flexSpace"/>
				<div>
					<p className="group_title" onClick={()=>openPick("spouse2Id")}>
						Conyuge (2)
					</p>
					<TextView  
						init={getStore("MCT_spouse2")?.["name"]} 
						label={"Nombre"} 
						width={240} 
					/>
				</div>
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<button className="group_title purple addBtn" onClick={save}>
					Guardar
				</button>
			</div>
			
		</div>
	);
};

export default MarriageCertification;

/*********************************************************************************************************************************************/
/**************************************   loadAuth load auht function and render google singin button ****************************************/
/*********************************************************************************************************************************************/







const TextView  = ({ init, label, width }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
		}
	});

	if(!init){
		return null
	}

	return (
		<div>
			<p className="label">
				{label}
			</p>
			<div  
				style={width? {width: width}: null}
				className="r7gAOb yyJm8b"
			>
				{init}
			</div>
		</div>
	)
}




const parseData2Spouse  = (v) => {
	return {
		"city_pob":	v?.placeOfBirth?.city,
		"state_pob":v?.placeOfBirth?.state,
		"fatherName":	v?.fatherInfo?.firstName ? `${v?.fatherInfo?.firstName} ${v?.fatherInfo?.middleName?v?.fatherInfo?.middleName+" ":""}${v?.fatherInfo?.lastName} ` :"",
		"dob":	v?.dateOfBirth,
		"dateOfBirth": parseDate(v?.dateOfBirth),
		"motherName": v?.motherInfo?.firstName ? `${v?.motherInfo?.firstName} ${v?.motherInfo?.middleName?v?.motherInfo?.middleName+" ":""}${v?.motherInfo?.lastName} ` :"",
		"name":v?.firstName ? `${v?.firstName} ${v?.middleName?v?.middleName+" ":""}${v?.lastName} ` :"",
	}

}